.checkoutPageHost {
  .checkoutTop {
    display: flex;
    width: 80%;
    margin-left: 70px;
    height: 80px;
    

    .logoContainer {
      margin: auto;

      img {
        margin-left: -200px;
      }
    }
  }

  .checkoutContent {
    background-color: white;
    padding-left: 150px;
    padding-right: 150px;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 200px;
  }

  .checkoutLeftContainer {
    width: 60%;

    .pageTitle {
      width: 100%;
      font-family: var(--app-text-main-font);
      font-size: 26px;
    }

    .sectionTitle {
      font-family: var(--app-text-secondary-font);
      font-size: 20px;
      margin-bottom: 20px;
      color:var(--app-color-gray-dark);
      font-weight: 500;
    }

    label {
      font-family: var(--app-text-secondary-font), serif;
      font-size: 14px;
      color:var(--app-color-gray-dark);
      font-weight: 500;
    }

    .inputRow {
      display: flex;
      min-width: 300px;
      .inputContainer {
        width: 100%;
        margin-right: 30px;
      }

      .cardContainer {
        width: 60%;
        margin-right: 10px;
      }

      .stripeCardNumberContainer {
        display: block;
        width: 100%;
        padding: 18px 10px;
        font-family: var(--app-text-secondary-font), serif;
        font-size: 16px;
        color: #4a4a4a;
        border: 1px solid var(--app-color-gray-lighter);
        font-weight: 400;
        line-height: 1.5;
        background-color: #fff;
        background-clip: padding-box;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        border-radius: 0px;
        transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
      }
    }

    .inputField {
      font-family: var(--app-text-secondary-font), serif;
      font-size: 16px;
      color: #4a4a4a;
      border-radius: 0px;
      border: 1px solid var(--app-color-gray-lighter);
      width: 100%;
      outline: none;
      height: 56px;
      margin-top: 5px;
      padding: 0 10px 0 10px;
    }

    .cardInput {
      font-family: var(--app-text-secondary-font), serif;
      font-size: 16px;
      color: var(--app-color-gray-dark);
      border-radius: 8px;
      border: 1px solid var(--app-color-gray-lighter);
      height: 56px;
    }

  }

  .checkoutRightContainer {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    margin: auto;
    .productsContainer {
      .hr {
        border-top: 1px solid #D2D2D2;
      }
      margin-top: 10px;
      width: 300px;
      padding-bottom: 10px;
      left: 925px;
      top: 121px;
      background: var(--app-color-gray-lighter);
      border-radius: 8px;

      .products {
        text-align: center;
        font-size: 24px;
        font-family: var(--app-text-main-font), serif;
        margin-left: auto;
        margin-right: auto;
        font-weight: 600;
        padding-top: 20px;
      }

      .planContainer {
        border-radius: 4px;
        margin: 5%;
        padding: 10px;
        background-color: white;

        .planTitle {
          color:var(--app-color-gray-dark);
          font-size: 18px;
          font-weight: 400;
          font-family: var(--app-text-secondary-font), serif;
          margin: 0;
        }
        
        .price {
          font-family: var(--app-text-secondary-font), serif;
          color: var(--app-color-gray-dark);
          margin: 0;
          font-weight: 400;
          font-size: 16px;
        }

        .planDetails {
          font-size: 12px;
          line-height: 15px;
          font-weight: 400;
          font-family: var(--app-text-secondary-font), serif;
          color:var(--app-color-gray-dark);
          margin: 0;
        }

        .priceDetails {
          line-height: 15px;
          font-family: var(--app-text-secondary-font), serif;
          color:var(--app-color-gray-dark);
          font-weight: 400;
          font-size: 12px;
          margin: 0;
        }
      }

    }

    .payButton {
      width: 90%;
      font-family: var(--app-text-secondary-font), serif;
      font-size: 16px;
      margin-left: auto;
      margin-right: auto;
      background-color: green;
      color: white;
    }
  }
}
.planTitleBlue {
  color:var(--app-color-gray-dark);
    font-size: 16px;
    font-weight: 600;
    font-family: var(--app-text-secondary-font), serif;
    margin: 0;  
 
}

.endingMessage {
  padding: 24px 24px 24px 24px;
  font-size: 12px;
  
  font-family: var(--app-text-secondary-font), serif;
  margin: 0;  

}
.currencySelectContainer {
  margin-top: 7px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 57px;
  border: 1px solid var(--app-color-gray-lighter);
  font-family: var(--app-text-main-font);
  border-radius: 8px;
  width: 100%;
}

.currencySelectComponent {
  border: none;
}

.currencySelectMenuOverlayList {
  left: 0px;
  right: 0;
  top: 80%;
  font-family: var(--app-text-main-font);
}

.currencySelectMenuOverlayListItem {
  padding: 5px;
}

.currencySearchInput {
  max-width: 80%;
  font-family: var(--app-text-main-font);
}

.phoneIInput {
  border: none!important;
  padding-left: 80px!important;
  height: 50px;
  width: 50px;
  :focus{
    outline: none !important;
    border:none !important;
    box-shadow: none !important;
}
}

.phoneContainer {
  margin-top: 7px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 53px;
  border: 1px solid var(--app-color-gray-lighter);
  font-family: var(--app-text-main-font);
  border-radius: 0px;
  width: 100%;
}
